import LottieWebCimo from '../../components/LottieWebCimo/lottie-web-cimo'

export default {
  name: 'logon',
  data () {
    return {
      isPwd: true,
      username: 'admin',
      password: '',
      defaultOptions: {
        path: 'https://assets9.lottiefiles.com/packages/lf20_vo0a1yca.json',
        loop: true
      },
      loading: false,
      percentage: 0,
      isLottieF: false
    }
  },
  components: { LottieWebCimo },
  methods: {
    logon () {
      this.loading = !this.loading
      if (this.username === 'admin') {
        sessionStorage.setItem('access_token', 972784674)
        sessionStorage.setItem('user_role', 'admin')
      }
      if (this.username === 'test') {
        sessionStorage.setItem('access_token', 972784674)
        sessionStorage.setItem('user_role', 'test')
      }
      this.timer = setTimeout(() => {
        this.$router.push('/')
        this.loading = !this.loading
        this.$q.notify({
          message: 'hi，cimo 欢迎回来',
          color: 'green',
          position: 'top',
          timeout: 1500
        })
      }, Math.random() * 3000 + 1000)
    },
    handleFinish (e) {
      this.isLottieF = e
    }
  }
}
